import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UserSettingsPage } from './user-settings.page';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {path: '', component: UserSettingsPage,
    children: [
      {path: 'create-user', loadChildren: () => import('./create-user/create-user.module').then(m => m.CreateUserModule)},
      {path: 'update-password', loadChildren: () => import('./update-password/update-password.module').then(m => m.UpdatePasswordModule)},
      {path: '', pathMatch: 'full', redirectTo: 'update-password'}

    ]
  },
];

@NgModule({
  declarations: [UserSettingsPage],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ]
})
export class UserSettingsModule { }
